.text-block {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 66px;

  &__img {
    max-width: vw(120px);
    max-height: vw(120px);
  }

  &__desc {
    font-size: vw(16px);
    line-height: vw(25px);
    color: #57555C;
    flex: 1;
    padding-left: vw(22px);
  }
}
